import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { io } from "socket.io-client";

const client = io("ws://176.104.33.48:8081");

function ChatHelper() {
    const [value, setValue] = useState("");
    const [message, setMessage] = useState([]);
    const [unicId, setUnicId] = useState([]);
    const [isOpenChat, setIsOpenChat] = useState(false);
    const [count, setCount] = useState(0);
    const [lastIdElem, setLastIdElem] = useState("");

    const hadleToggleChat = () => {
        setIsOpenChat((prevState) => !prevState);
        setCount(0);

        if (!isOpenChat) {
            console.log(" ");
            setTimeout(() => {
                lastMessageScroll("smooth");
                setLastIdElem("");
            }, 500);
        } else {
            console.log(" ");
        }

        // if (!isOpenChat) {
        //     lastMessageScroll();
        // }
    };
    useEffect(() => {
        if (localStorage.inicId) {
            setUnicId(localStorage.inicId);
        } else {
            setUnicId(Date.now());
            localStorage.setItem("inicId", unicId);
        }

        client.on("message", (msg) => {
            // console.log(msg);
            // setCount((prevState) => prevState++);
            if (!isOpenChat) {
                setCount((prevState) => prevState + 1);
                if (lastIdElem === "") {
                    setLastIdElem(msg.id);
                }
            }
            setMessage((prevState) => [...prevState, msg]);
        });
    }, []);

    const handleChange = (e) => {
        setValue(e.target.value);
    };

    const handleKeyDown = (e) => {
        if (e.keyCode === 13) {
            onSendMessage();
        }
    };
    function lastMessageScroll(b) {
        const e = document.querySelector(`#a${lastIdElem}`);
        if (!e) return;
        console.log("test e = ", e);

        e.scrollIntoView({
            behavior: b || "auto",
            block: "end"
        });
    }

    useEffect(() => {}, []);

    const onSendMessage = () => {
        if (value !== "") {
            const sendMessage = {
                id: Date.now(),
                text: value,
                author: "",
                isSendMe: false
            };
            setMessage((prevState) => [...prevState, sendMessage]);
            setValue("");

            client.emit("sendMessage", value);
        }
    };

    return (
        <>
            <section className="_chat_helper">
                {!isOpenChat && (
                    <div
                        className="_chat_helper_button"
                        onClick={hadleToggleChat}
                    >
                        {count !== 0 && (
                            <div className="_chat_helper_button_img">
                                <span>{count}</span>
                            </div>
                        )}
                    </div>
                )}
                {isOpenChat && (
                    <div className="_chat_helper_container">
                        <div className="_header">
                            <span onClick={hadleToggleChat}></span>
                        </div>
                        <div className="_body">
                            <ul>
                                {message &&
                                    message.map((el) => (
                                        <li
                                            key={el.id}
                                            // className={
                                            //     "_body_message " + el.isSendMe
                                            //         ? "_body_message_send_me"
                                            //         : "_body_message_not_send_me"
                                            // }
                                            className={
                                                "_body_message " +
                                                (el.isSendMe
                                                    ? "_body_message_send_me"
                                                    : "_body_message_not_send_me")
                                            }
                                            id={`a${el.id}`}
                                        >
                                            {/* <span>{el.author}</span> */}
                                            <p>{el.text}</p>
                                        </li>
                                    ))}
                                <li class="wrapper_Scrollbottom"></li>
                            </ul>
                        </div>
                        <div className="_footer">
                            <input
                                type={"textarea"}
                                id={"text"}
                                value={value}
                                placeholder={" "}
                                aria-label={"text"}
                                // name={name}
                                onChange={handleChange}
                                onKeyDown={handleKeyDown}
                                className={"_footer_input"}
                            >
                                {/* <button onClick={onSendMessage}>Send</button> */}
                            </input>
                            <span className="btn_send" onClick={onSendMessage}>
                                <i></i>
                            </span>
                            {/* <button onClick={onSendMessage}>Send</button> */}
                            {/* <button onClick={onUpdate}>Update</button> */}
                        </div>
                    </div>
                )}
            </section>
        </>
    );
}

ChatHelper.propTypes = {
    demo: PropTypes.bool
};

export default ChatHelper;
