import React from "react";
import PropTypes from "prop-types";
import * as CONST from "../js/config.js";

function Slide2({ demo, language }) {
    if (language === "en") {
        return (
            <>
                <section className="slide-2" id={demo ? "" : "slide2"}>
                    <div className="slide2-container">
                        <div className="image"></div>
                        <div className="slide2-content">
                            <h2>
                                My Telegram and YouTube are very disciplined.
                                Reviews every day! (NY time)
                            </h2>
                            <p>
                                7:35 PM review on ES (SP500), NQ (Nasdaq), BRN
                                (Brent), GC (Gold), NG (Natural Gas)
                            </p>
                            <p>
                                03:00 AM overview of BTC, BCH, ETH, SOL, XRP and
                                ADA to usdt
                            </p>
                            <p>
                                15:00   MX (Imoex), Ri (RTS), Si
                                (UsdRub), SR (Sber), GZ (Gazprom), VB (VTBR),
                                Mgnt (Magnit), YNDX (Yandex)
                            </p>
                            <p>
                                Subscribe to my free{" "}
                                <a
                                    href={CONST.YT_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    YouTube
                                </a>{" "}
                                and{" "}
                                <a
                                    href={CONST.TG_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    Telegram
                                </a>
                            </p>
                        </div>
                    </div>
                </section>
            </>
        );
    }

    if (language === "ua") {
        return (
            <>
                <section className="slide-2" id={demo ? "" : "slide2"}>
                    <div className="slide2-container">
                        <div className="image"></div>
                        <div className="slide2-content">
                            <h2>
                                 Telegram  YouTube  .
                                 !
                            </h2>
                            <p>
                                0:35   ES (SP500), NQ (Nasdaq), BRN
                                (Brent), GC (Gold), NG (Natural Gas)
                            </p>
                            <p>
                                10:00   BTC, BCH, ETH, SOL, XRP  ADA 
                                usdt
                            </p>

                            <p>
                                   {" "}
                                <a
                                    href={CONST.YT_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    YouTube
                                </a>{" "}
                                {" "}
                                <a
                                    href={CONST.TG_CHANNEL}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    Telegram
                                </a>
                            </p>
                        </div>
                    </div>
                </section>
            </>
        );
    }

    return (
        <>
            <section className="slide-2" id={demo ? "" : "slide2"}>
                <div className="slide2-container">
                    <div className="image"></div>
                    <div className="slide2-content">
                        <h2>
                             Telegram  YouTube  .
                              !
                        </h2>
                        <p>
                            0:35   ES (SP500), NQ (Nasdaq), BRN (Brent),
                            GC (Gold), NG (Natural Gas)
                        </p>
                        <p>
                            10:00   BTC, BCH, ETH, SOL, XRP  ADA  usdt
                        </p>
                        <p>
                            15:00   MX (Imoex), Ri (RTS), Si (UsdRub), SR
                            (Sber), GZ (Gazprom), VB (VTBR), Mgnt (Magnit), YNDX
                            (Yandex)
                        </p>
                        <p>
                               {" "}
                            <a
                                href={CONST.YT_CHANNEL}
                                target="_blank"
                                rel="noreferrer"
                            >
                                YouTube
                            </a>{" "}
                            {" "}
                            <a
                                href={CONST.TG_CHANNEL}
                                target="_blank"
                                rel="noreferrer"
                            >
                                Telegram
                            </a>
                        </p>
                    </div>
                </div>
            </section>
        </>
    );
}

Slide2.propTypes = {
    demo: PropTypes.bool,
    language: PropTypes.string.isRequired
};

export default Slide2;
