import React from "react";
import PropTypes from "prop-types";

function Language({
    onChangeLanguageRu,
    onChangeLanguageEn,
    onChangeLanguageUa
}) {
    return (
        <>
            <section className="language">
                <div className="buttons">
                    <div
                        className="buttons-ru"
                        onClick={onChangeLanguageRu}
                    ></div>
                    <div
                        className="buttons-en"
                        onClick={onChangeLanguageEn}
                    ></div>
                    <div
                        className="buttons-ua"
                        onClick={onChangeLanguageUa}
                    ></div>
                </div>
            </section>
        </>
    );
}

Language.propTypes = {
    onChangeLanguageRu: PropTypes.func,
    onChangeLanguageEn: PropTypes.func,
    onChangeLanguageUa: PropTypes.func
};

export default Language;
