import React, { useState, useEffect } from "react";
import Navigation from "../components/navigation.jsx";
import Language from "../components/language.jsx";
import Slide1 from "../components/slide_1.jsx";
import Slide2 from "../components/slide_2.jsx";
import Slide3 from "../components/slide_3.jsx";
import Slide4 from "../components/slide_4.jsx";
import Slide5 from "../components/slide_5.jsx";
import Slide6 from "../components/slide_6.jsx";
import Slide7 from "../components/slide_7.jsx";
import Slide8 from "../components/slide_8.jsx";
import Slide9 from "../components/slide_9.jsx";
import Slide10 from "../components/slide_10.jsx";
import Slide11 from "../components/slide_11.jsx";
import Slide12 from "../components/slide_12.jsx";
import ChatHelper from "../components/chatHelper.jsx";

function Main() {
    const [language, setLanguage] = useState("ru");

    useEffect(() => {
        if (localStorage.language) {
            setLanguage(localStorage.language);
        }
    }, []);

    const handleChangeLanguageRu = () => {
        localStorage.setItem("language", "ru");
        setLanguage("ru");
    };

    const handleChangeLanguageEn = () => {
        localStorage.setItem("language", "en");
        setLanguage("en");
    };

    const handleChangeLanguageUa = () => {
        localStorage.setItem("language", "ua");
        setLanguage("ua");
    };

    return (
        <>
            <Language
                onChangeLanguageEn={handleChangeLanguageEn}
                onChangeLanguageRu={handleChangeLanguageRu}
                onChangeLanguageUa={handleChangeLanguageUa}
            />
            <Navigation language={language} />
            <Slide1 language={language} />
            <Slide2 language={language} />
            <Slide3 language={language} />
            <Slide4 language={language} />
            <Slide5 language={language} />
            <Slide6 language={language} />
            <Slide7 language={language} />
            <Slide8 language={language} />
            <Slide9 language={language} />
            <Slide10 language={language} />
            <Slide11 language={language} />
            <Slide12 language={language} />
            <ChatHelper />
        </>
    );
}

export default Main;
