import React from "react";
import { Navigate, Route, Routes, BrowserRouter } from "react-router-dom";
import Main from "./layouts/main";

function App() {
    return (
        <BrowserRouter>
            <Routes>
                <Route path="/" element={<Main />} />
                <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
        </BrowserRouter>
    );
}

export default App;
